.header{
    width: 100%;
    height: 50px;
    background-color: #C4C4C4;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.img{
    width: 25px;
    height: 25px;
    margin-right: 20px;
    margin-left: 10px;
}
.img > img{
    width: 100%;
    height: 100%;
}
.img:hover{
    cursor: pointer;
}
.basket{
    display: flex;
    align-items: center;

}
.button{
    display: flex;
}