.price{
    display: flex;
    justify-content: space-between;
}
.basket{
    width: 25px;
    height: 25px;
}
.basket > img{
    width: 100%;
    height: 100%;
}
.basket > img:hover{
    cursor: pointer;
}
.img{
    width: 200px;
    height: 200px;
}
.img > img{
    width: 100%;
    height: 100%;
}
.products{
    width: 80%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}